<template>
  <el-popover
      placement="left"
      width="400"
      trigger="click">
    <el-tree
        ref="popoverTree"
        :data="treeData"
        node-key="orgId"
        accordion
        :show-checkbox="false"
        :highlight-current="true"
        :expand-on-click-node="true">
      <span class="custom-tree-node" slot-scope="{ node, data }">
        <span>{{ data.orgName }}</span>
        <span v-if="node.isLeaf && node.level !== 1 && data.orgId !== dialogForm.orgId">
          <el-button type="text" size="mini"
                     @click="(event) => editStaffOrg(event, data, node)">更改</el-button>
        </span>
      </span>
    </el-tree>
    <el-button type="text" slot="reference" style="margin-left: 10px">调整组织</el-button>
  </el-popover>
</template>

<script>
  import {makeTree} from '@common/util'
  export default {
    name: "staffPopover",
    props: ['dialogForm'],
    data() {
      return {
        treeData: [],
      }
    },
    created() {
      this.initTreeNodes();
    },
    methods: {
      async initTreeNodes() {
        this.treeData = [];
        let result = await this.$api.staffs.getOrganizationList();
        this.treeData = makeTree('orgId', 'parentOrgId', result.data.data);
      },
      clickNode() {
      
      },
      editStaffOrg(event, data, node) {
        this.$confirm(`此操作将修改员工【${this.dialogForm.staffName}】的组织，是否继续？`, '取消', {
          type: 'warning'
        }).then(() => {
          this.$emit('editStaffOrg', data.orgId, this.dialogForm)
        })
      }
    }
  }
</script>

