<template>
  <el-card shadow="always" v-show="show === true">
    <div slot="header">
      <span>员工信息</span>
    </div>
    <el-form :inline="true" :model="searchForm">
      <el-col :span="24">
        <el-form-item label="查询">
          <el-input v-model="searchForm.fuzzyQuery" placeholder="工号/姓名模糊查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-loading="loading.search" @click="handleQuery">查询</el-button>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-table :data="tableData" highlight-current-row v-loading="loading.table" style="width: 100%" border>
          <el-table-column prop="staffId" label="员工id" show-tooltip-when-overflow></el-table-column>
          <el-table-column prop="staffAccount" label="工号" show-overflow-tooltip></el-table-column>
          <el-table-column prop="staffName" label="姓名" show-overflow-tooltip></el-table-column>
          <el-table-column prop="postCode" min-width="120px" label="岗位" show-overflow-tooltip>
            <template slot-scope="scope">
              <span>{{ postCodeFormat(scope.row.postCode) }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="mobile" min-width="150px" label="手机" show-overflow-tooltip></el-table-column>
          <el-table-column prop="email" min-width="200px" label="邮箱" show-overflow-tooltip></el-table-column>
          <el-table-column prop="initRoleId" min-width="150px" label="是否创建账号">
            <template slot-scope="scope">
              <span>{{ staffIdFormat(scope.row.initRoleId) }}</span>
            </template>
          </el-table-column>
          <el-table-column fixed="right" min-width="200px" label="操作">
            <template slot-scope="scope">
              <el-button type="text" @click="editStaff(scope.row)">编辑</el-button>
              <el-button type="text" @click="deleteStaff(scope.row)">删除</el-button>
<!--              <el-button type="text" @click="editStaff(scope.row)">调整组织</el-button>-->
              <staff-popover :dialog-form="scope.row" @editStaffOrg="editStaffOrg"></staff-popover>
            </template>
          </el-table-column>
        </el-table>
        <div class="el-row ac mt20">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-size=10
              :page-sizes="[10, 20, 30]"
              :current-page="searchForm.pageNum || 1"
              background
              layout="total, sizes, prev, pager, next, jumper"
              :total=tableTotal>
          </el-pagination>
        </div>
      </el-col>
    </el-form>
  </el-card>
</template>

<script>
  import staffPopover from './staffPopover';
  export default {
    name: 'StaffForm',
    components: {
      'staff-popover': staffPopover,
    },
    props: ['show', 'tableForm', 'selects'],
    data() {
      return {
        border: true,
        loading: {
          search: false,
          table: false,
        },
        searchForm: {
          fuzzyQuery: '',
          orgId: '',
          pageSize: 10,
          pageNum: 1,
        },
        tableData: [],
        tableTotal: 0,
      };
    },
    watch: {
      'tableForm.orgId'(newV, oldV) {
        this.searchForm.fuzzyQuery = '';
        this.searchForm.orgId = newV;
        this.searchForm.pageSize = 10;
        this.searchForm.pageNum = 1;
        this.initTable(this.searchForm);
      },
    },
    methods: {
      async initTable(param) {
        this.loading.table = true;
        let response = await this.$api.staffs.getStaffList(param);
        this.loading.table = false;
        this.tableData = response.data.data;
        this.tableTotal = this.tableData ? this.tableData.length : 0;
      },
      handleSizeChange() {

      },
      handleCurrentChange() {

      },
      handleQuery() {
        this.initTable(this.searchForm);
      },
      postCodeFormat(postCode) {
        let index = this.selects.postCodeArr.findIndex( (item) => item.value === postCode);
        return index !== -1 ? this.selects.postCodeArr[index].label : '';
      },
      staffIdFormat(initRoleId) {
        return initRoleId ? '是' : '否';
      },
      editStaff(row) {
        this.$emit('editStaff', row);
      },
      async editStaffOrg(newOrgId, row) {
        let param = {
          remark: '',
          email: '',
          gender: '',
          initRoleId: '',
          mobile: '',
          orgId: '',
          postCode: '',
          staffAccount: '',
          staffId: '',
          staffName: '',
        };
        for (let key in param) {
          param[key] = key === 'orgId' ? newOrgId : row[key] ? row[key] : '';
        }
        let response = await this.$api.staffs.editStaff(param);
        this.$message({
          showClose: true,
          message: response.data.meta.message,
          type: response.data.meta.success ? 'success' : 'error'
        });
        this.initTable(this.searchForm);
      },
      deleteStaff(row) {
        this.$confirm(`此操作将删除员工【${row.staffName}】，是否继续？`, '删除', {
          type: 'warning'
        }).then( async () => {
          let response = await this.$api.staffs.deleteStaff(row.staffId);
          this.$message({
            showClose: true,
            message: response.data.meta.message,
            type: response.data.meta.success ? 'success' : 'error'
          });
          this.initTable(this.searchForm);
        });
      }
    }
  };
</script>

<style>
  .ac {
    text-align: center;
  }
.el-table th{
   background-color:#e8f1f9;
   font-weight: 545;
   text-align: center;
}
  .el-table td{
    text-align: center;
  }

</style>
