<template>
  <el-container style="margin-top: -10px">
    <el-main>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-card shadow="always">
            <div slot="header">
              <span>员工管理</span>
            </div>
            <staff-tree
                :showActionButton="true"
                :ref="refs.tree"
                @clickNode="clickNode"
                @addStaff="addStaff">
            </staff-tree>
          </el-card>
        </el-col>
        <el-col :span="16">
          <staff-table
              :show.sync="tableForm.show"
              :table-form="tableForm"
              :ref="refs.table"
              :selects="selects"
              @editStaff="editStaff"
          ></staff-table>
        </el-col>
        <el-col :span="24" style="margin-top: 10px">
          <staff-dialog
              :ref="refs.dialog"
              :selects="selects"
              :dialog-form="dialogForm"
              @closeDialog="closeDialog">
          </staff-dialog>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
  import StaffTree from './StaffTree';
  import StaffTable from './StaffTable';
  import StaffDialog from './StaffDialog';

  export default {
    name: 'StaffSection',
    components: {
      'staff-table': StaffTable,
      'staff-tree': StaffTree,
      'staff-dialog': StaffDialog,
    },
    data() {
      return {
        refs: {
          tree: 'tree',
          table: 'table',
          dialog: 'dialog',
        },
        tableForm: {
          show: false,
          fuzzyQuery: '',
          orgId: '',
        },
        dialogForm: {
          remark: '',
          email: '',
          gender: '',
          initRoleId: '',
          mobile: '',
          orgId: '',
          postCode: '',
          staffAccount: '',
          staffId: '',
          staffName: '',
        },
        selects: {
          roleArr: [],
          genderArr: [],
          postCodeArr: [],
        }
      };
    },
    methods: {
      resetDialogForm() {
        this.dialogForm = {
          remark: '',
          email: '',
          gender: '',
          initRoleId: '',
          mobile: '',
          orgId: '',
          postCode: '',
          staffAccount: '',
          staffId: '',
          staffName: '',
        };
      },
      addStaff(event, data, node) {
        event.stopPropagation();
        this.resetDialogForm();
        this.dialogForm.orgId = data.orgId;
      },
      editStaff(row) {
        this.resetDialogForm();
        for (let key in this.dialogForm) {
          this.dialogForm[key] = row[key] ? row[key] : '';
        }
      },
      closeDialog() {
        this.resetDialogForm();
        this.$refs[this.refs.table].handleQuery();
      },
      clickNode(data, node, self) {
        if (node.isLeaf && node.level !== 1) {
          this.tableForm.show = true;
          this.tableForm.orgId = data.orgId;
          this.tableForm.fuzzyQuery = '';
        }
      }
    },
    created() {
      let that = this;
      Promise.all([
        this.$api.roles.getRoleByPage(1000, 1, null),
        this.$api.staffs.getDic('STAFF', 'GENGER'),
        this.$api.staffs.getDic('STAFF', 'POST_TYPE'),
      ]).then((result) => {
        // 角色、性别、岗位
        that.selects.roleArr = result[0].data.data.list;
        that.selects.genderArr = result[1].data.data;
        that.selects.postCodeArr = result[2].data.data;
      });
    }
  };
</script>

