<template>
  <div v-loading="loading">
    <el-row class="filter-query">
      <el-col :span="6">
        <span class="filter-span">组织名称</span>
      </el-col>
      <el-col :span="12" :offset="1">
        <el-input placeholder="组织机构" v-model="orgFilter"></el-input>
      </el-col>
    </el-row>
    <el-tree
        :ref="ref"
        :data="treeData"
        node-key="orgId"
        accordion
        :show-checkbox="false"
        :highlight-current="true"
        :expand-on-click-node="false"
        :filter-node-method="filterNode"
        @node-click="clickNode">
      <span class="custom-tree-node" slot-scope="{ node, data }">
        <span>{{ data.orgName }}</span>
        <span v-if="showActionButton && node.isLeaf && node.level !== 1">
          <el-button type="text" size="mini"
                     @click="(event) => addStaff(event, data, node)">新增员工</el-button>
        </span>
      </span>
    </el-tree>
  </div>
</template>

<script>
  import {makeTree} from '@common/util'
  export default {
    name: "StaffTree",
    props: {
      showActionButton: {
        type: Boolean,
        require: false,
        default: false
      },
    },
    data() {
      return {
        ref: 'tree',
        loading: false,
        orgFilter: '',
        treeData: [],
      }
    },
    created() {
      this.initTreeNodes();
    },
    watch: {
      'orgFilter'(newV, oldV) {
        this.$refs[this.ref].filter(newV)
      }
    },
    methods: {
      async initTreeNodes() {
        this.treeData = [];
        this.loading = true;
        let result = await this.$api.staffs.getOrganizationList();
        this.loading = false;
        this.treeData = makeTree('orgId', 'parentOrgId', result.data.data);
      },
      filterNode(value, data) {
        if (!value) {
          return true
        }
        return data.orgName.indexOf(value) !== -1
      },
      addStaff(event, data, node) {
        this.$emit('addStaff', event, data, node);
      },
      clickNode(data, node, self){
        this.$emit('clickNode', data, node, self)
      }
    }
  }
</script>

<style>
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 12px;
  }
  
  .filter-query {
    margin-bottom: 20px;
  }
  
  .filter-query .filter-span {
    height: 40px;
    line-height: 40px;
  }
  
  .filter-query .filter-btn {
    height: 40px;
    line-height: 40px;
  }

</style>