<template>
  <el-dialog
      :title="dialog.title"
      :visible.sync="dialog.visible"
      :fullscreen="true"
      ref="dialog"
      @close="close"
      @open="afterOpen"
      width="80%">
    <el-form :model="form" :rules="rules" ref="dialogForm" @submit.native.prevent label-width="100px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="工号" prop="staffAccount">
            <el-input v-model="form.staffAccount" placeholder="请输入工号"></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="staffName">
            <el-input v-model="form.staffName" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="岗位" prop="">
            <el-select v-model="form.postCode" placeholder="请选择岗位" style="width: 100%">
              <el-option v-for="item in selects.postCodeArr" :value="item.value" :label="item.label" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="性别" prop="">
            <el-select v-model="form.gender" placeholder="请选择性别" style="width: 100%">
              <el-option v-for="item in selects.genderArr" :value="item.value" :label="item.label" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="手机" prop="">
            <el-input v-model="form.mobile" placeholder="请输入手机号码"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="">
            <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item label="备注" prop="">
            <el-input type="textarea" v-model="form.remark" placeholder="备注"></el-input>
          </el-form-item>
          <div v-show="dialog.isAdd">
            <el-form-item label="" prop="">
              <el-checkbox v-model="createRoleFlag">是否创建账号</el-checkbox>
            </el-form-item>
            <el-form-item label="角色" prop="" v-show="createRoleFlag">
              <el-select v-model="form.initRoleId" placeholder="请选择角色" style="width: 100%">
                <el-option v-for="item in selects.roleArr" :value="item.roleId" :label="item.roleName" :key="item.roleId"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">提交</el-button>
            <el-button @click="reset">重置</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </el-dialog>
</template>

<script>
  export default {
    name: "StaffDialog",
    props: {
      'dialogForm': {
        type: Object,
        default: {}
      },
      'selects': {
        type: Object,
        default: {}
      }
    },
    created() {
    
    },
    data() {
      return {
        createRoleFlag: false,
        dialog: {
          title: '',
          isAdd: false,
          visible: false,
        },
        form: {
          remark: '',
          email: '',
          gender: '',
          initRoleId: '',
          mobile: '',
          orgId: '',
          postCode: '',
          staffAccount: '',
          staffId: '',
          staffName: '',
        },
        rules: {
          staffName: [{
            required: true,
            message: '姓名不能为空',
            trigger: 'blur'
          }],
          staffAccount: [{
            required: true,
            message: '工号不能为空',
            trigger: 'blur'
          }],
        }
      }
    },
    methods: {
      showAddStaffDialog() {
        this.dialog.title = '新增员工';
        this.dialog.isAdd = true;
        this.dialog.visible = true;
      },
      showEditStaffDialog() {
        this.dialog.title = '修改员工';
        this.dialog.isAdd = false;
        this.dialog.visible = true;
      },
      onSubmit() {
        this.$refs.dialogForm.validate(async (valid) => {
          if (valid) {
            let {staffId, ...addParam} = this.form;
            let response = await (
              this.dialog.isAdd
                ? this.$api.staffs.addStaff(addParam)
                : this.$api.staffs.editStaff(this.form)
            )
            
            this.$message({
              showClose: true,
              message: response.data.meta.message,
              type: response.data.meta.success ? 'success' : 'error'
            });
            if (response.data.meta.success) {
              this.close();
            }
          }
        })
      },
      reset() {
        for (let key in this.form) {
          if (key !== 'orgId' && key !== 'staffId') {
            this.form[key] = '';
          }
        }
      },
      close() {
        this.dialog.visible = false;
        this.$emit('closeDialog');
      },
      afterOpen() {

      },
    },
    watch: {
      'dialogForm': {
        deep: true,
        immediate: true,
        handler(newV, oldV) {
          this.createRoleFlag = newV.initRoleId ? true : false;
          if (newV.orgId && newV.staffId !== '') {
            this.showEditStaffDialog();
          } else if (newV.orgId && newV.staffId === '') {
            this.showAddStaffDialog();
          }
          for (let key in newV) {
            this.form[key] = newV[key];
          }
        }
      }
    },
  }
</script>

<style>
  .el-textarea__inner {
    font-family: "微软雅黑";
    /*padding-top: 7px !important;*/
  }

</style>